import React from "react";
import { useSelector } from "react-redux";
//import MUI components
import { Stack } from "@mui/material";

//import components
import Column from "../Components/Column/Column";
import { boardData } from "../Redux/board/board-slice";

import { useGetColumnsQuery } from "../Redux/columns/column-operations";

import useWindowDimensions from "../libs/useWindowDimentions";

export default function ColumnsView() {
  const boardDataFromState = useSelector(boardData);
  const aitiveBoardId = boardDataFromState.id;

  const { data: columns } = useGetColumnsQuery(aitiveBoardId);

  const [width, height] = useWindowDimensions();

  return (
    <Stack
      sx={{ height: `${height - 64}` }}
      mt={2}
      mx={2}
      direction="row"
      justifyContent="flex-start"
      alignItems="flex-start"
      spacing={3}
    >
      {!!columns &&
        columns.map((column) => {
          const { id, name, position_on_board } = column;
          return <Column key={id} height={height} id={id} />;
        })}
    </Stack>
  );
}
