import React, { useState } from "react";

//import MIU components
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import TaskCard from "../TaskCard";
import Box from "@mui/material/Box";

//import components
import ChangeChipsPallet from "../../ChangeChips/ChangeChipsPallet";
import ChangeUsers from "../ChangeUsers/ChangeUsers";
import DatesAndTimePallet from "../../DateAndTime/DatesAndTimePallet";

const buttonStyle = {
  height: "32px",
  fontSize: "14px",
  lineHeight: "1.5",
  marginBottom: "10px",
  backgroundColor: "#d7d7d7",
  color: "black",
  "&:hover": {
    backgroundColor: "#e3e3e3",
  },
};

export default function EditButtonsGroup({ handleClickOpen }) {
  const [typePop, setTypePop] = useState(0);

  function closeHandle() {
    setTypePop(0);
  }

  function Pop(type) {
    let pop = null;

    switch (type) {
      case 1:
        pop = <ChangeChipsPallet closeHandle={closeHandle} />;
        break;

      case 2:
        pop = <ChangeUsers closeHandle={closeHandle} />;
        break;
      // case 3:
        // pop = <ChangeUsers closeHandle={closeHandle} />;
        // break;
      case 4:
        pop = <DatesAndTimePallet closeHandle={closeHandle} />;
        break;

      default:
        break;
    }
    return <Box sx={{ margin: "10px" }}>{pop}</Box>;
  }

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        flexWrap: "nowrap",
        alignItems: "flex-start",
        margin: "10px",
      }}
    >
      <TaskCard />
      {typePop === 0 ? (
        <Box sx={{ margin: "10px" }}>
          <Stack direction="column" justifyContent="center" alignItems="flex-start" spacing={1}>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              onClick={() => handleClickOpen(1)}
            >
              Открыть карточку
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              onClick={() => setTypePop(1)}
            >
              Изменить метки
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              onClick={() => setTypePop(2)}
            >
              Изменить участников
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              // onClick={() => setTypePop(3)}
            >
              Сменить обложку
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              onClick={() => setTypePop(4)}
            >
              Изменить даты
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              // onClick={HandleAddNewRowToCheckList}
            >
              Переместить
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              sx={buttonStyle}
              // onClick={HandleAddNewRowToCheckList}
            >
              Архивировать
            </Button>
          </Stack>
        </Box>
      ) : null}

      {Pop(typePop)}
    </Box>
  );
}
