import React, { useState } from "react";
import Draggable from "react-draggable";

import "@fontsource/roboto/400.css";
//import MIU components
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import IconButton from "@mui/material/IconButton";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Paper from "@mui/material/Paper";

//import MIU icons
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import CloseOutlinedIcon from "@mui/icons-material/CloseOutlined";

// import components
import CardImage from "./CardImageSection/CardImage";
import CardDescription from "./CardDescriptionSection/CardDescription";
import CardStatistic from "./CardStatisticIconsSection/CardStatistic";
import CardAvatarSection from "./Avatar/CardAvatarSection";
import CardChipContainer from "./CardChipsSection/CardChipContainer";
import AllSettingsOfCard from "./AllSettingsOfCard/AllSettingsOfCard";
import EditButtonsGroup from "./EditButtonsGroup/EditButtonsGroup";

import { useGetCardsQuery } from "../../Redux/cards/cards-operations";

function PaperComponent(props) {
  return (
    <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
      <Paper
        sx={{
          borderTopLeftRadius: "16px",
          borderTopRightRadius: "16px",
          backgroundColor: "rgba(66, 66, 70, 0.82)",
        }}
        {...props}
      />
    </Draggable>
  );
}

function Pop(type, handleClickOpen) {
  let pop = null;

  switch (type) {
    case 1:
      pop = <AllSettingsOfCard />;
      break;
    case 2:
      pop = <EditButtonsGroup handleClickOpen={handleClickOpen} />;
      break;

    default:
      break;
  }
  return pop;
}

const cardStyle = {
  display: "block",
  position: "relative",
  borderRadius: "20px",
  margin: "0px",
  padding: "0px",
  width: "256px",
  cursor: "pointer",
  height: "fit-content",
  "&:hover": {
    boxShadow: "3",
  },
};

const CardContentStyle = {
  display: "flex",
  flexDirection: "column",
  flexWrap: "wrap",
  minHeight: "24px",
  paddingTop: "8px",
  paddingX: "12px",
  paddingBottom: "4px",
};

const boxEditOutlinedIconStyle = {
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  position: "absolute",
  top: "8px",
  right: "8px",
  width: "40px",
  height: "40px",
  borderRadius: "20px",

  backgroundColor: "#eaeaea",
  "&:hover": {
    backgroundColor: "#dbdbdb",
  },
};

export default function TaskCard({
  board,
  checklist_how_many,
  column,
  comments_how_many,
  created,
  date_time_finish,
  date_time_start,
  header_color,
  header_image,
  id,
  in_process,
  is_archived,
  is_subscribed,
  is_have_description,
  name,
  position_in_column,
  text,
  status,
  updated,
}) {
  const { data: cardData } = useGetCardsQuery(id);

  const [showEditIcon, setShowEditIcon] = useState(false);
  const [openPop, setOpenPop] = useState(false);
  const [popupType, setPopupType] = useState(0);

  const handleClickOpen = (type) => {
    setOpenPop(true);
    switch (type) {
      case 1:
        setPopupType(type);
        break;
      case 2:
        setPopupType(type);
        break;
      default:
        setPopupType(0);
    }
  };

  const handleClose = () => {
    setOpenPop(false);
    setPopupType(0);
  };

  return (
    <>
      <Card
        sx={cardStyle}
        onMouseOver={() => setShowEditIcon(true)}
        onMouseOut={() => setShowEditIcon(false)}
      >
        {showEditIcon && (
          <Box sx={boxEditOutlinedIconStyle}>
            <IconButton onClick={() => handleClickOpen(2)} aria-label="edit">
              <EditOutlinedIcon />
            </IconButton>
          </Box>
        )}
        <Box onClick={() => handleClickOpen(1)}>
          <CardImage />
          <CardContent sx={CardContentStyle}>
            <CardChipContainer cardId={id} />
            <CardDescription cardText={text} />
            <CardStatistic
              cardId={id}
              is_subscribed={is_subscribed}
              in_process={in_process}
              comments_how_many={comments_how_many}
              checklist_how_many={checklist_how_many}
              is_have_description={is_have_description}
              date_time_finish={date_time_finish}
              date_time_start={date_time_start}
              status={status}
            />
            <CardAvatarSection />
          </CardContent>
        </Box>
      </Card>

      <Dialog
        sx={{ height: "100%", backgroundColor: "rgba(0, 0, 0, 0.55)" }}
        // scroll="body"
        maxWidth={"xl"}
        open={openPop}
        onClose={handleClose}
        PaperComponent={PaperComponent}
        aria-labelledby="draggable-dialog-title"
      >
        <DialogTitle
          style={{
            background: "linear-gradient(rgb(93, 93, 96), rgba(21, 21, 23, 0.04))",
            paddingTop: "6px",
            paddingBottom: "8px",
            cursor: "move",
            display: "inline-flex",
            flexFlow: "row",
            flexDirection: "row-reverse",
            borderTopLeftRadius: "16px",
            borderTopRightRadius: "16px",
          }}
          id="draggable-dialog-title"
        >
          <IconButton
            aria-label="close"
            sx={{ backgroundColor: "#777", "&:hover": { backgroundColor: "#ccc" } }}
            onClick={handleClose}
          >
            <CloseOutlinedIcon sx={{ color: "#fff" }} />
          </IconButton>
        </DialogTitle>
        <DialogContent
          sx={{
            maxWidth: "768px",
            // maxWidth: "fit-content",
            overflowY: "auto",
            overflowX: "hidden",
            msOverflowStyle: "none" /* IE и Edge */,
            scrollbarWidth: "none" /* Firefox */,
            "&::-webkit-scrollbar": {
              display: "none",
            },
            backgroundColor: "#f5f5f5",
            paddingX: "0px",
          }}
        >
          {Pop(popupType, handleClickOpen)}
        </DialogContent>
      </Dialog>
    </>
  );
}
