import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

//import MUI components
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListSubheader from "@mui/material/ListSubheader";

// import components
import TaskCard from "../Card/TaskCard";

import { useGetCardsQuery } from "../../Redux/cards/cards-operations";
import { columnData } from "../../Redux/columns/column-slice";

export default function Column({ width, height, id }) {
  const {data: cards} = useGetCardsQuery(id)
  const { columns } = useSelector(columnData);


  return (
    <List
      sx={{
        display: "flex",
        flexDirection: "column",
        flexWrap: "nowrap",
        alignItems: "center",
        alignContent: "center",
        width: "100%",
        maxWidth: "280px",
        maxHeight: `${height - 90}px`,
        marginTop: "10px",
        borderRadius: "4px",
        bgcolor: "gray",
        boxShadow: "10",
        overflow: "auto",
        msOverflowStyle: "none" /* IE и Edge */,
        scrollbarWidth: "none" /* Firefox */,
        "&::-webkit-scrollbar": {
          display: "none",
        },
      }}
      subheader={
        <ListSubheader
          sx={{
            width: "100%",
            height: "40px",
            textAlign: "center",
          }}
          titletypographyprops={{
            fontSize: "20px",
            color: "#172b4d",
          }}
        >
          {"В работе"}
        </ListSubheader>
      }
    >
      {
      !!cards &&
      cards.map((card) => {
        const { id, name, position_on_board } = card;
        return (
          <ListItem key={id}>
            <TaskCard {...card}/>
          </ListItem>
        );
      })}
    </List>
  );
}
