import React, { useState } from "react";
import CardChip from "../Card/CardChipsSection/CardChip";
import Box from "@mui/material/Box";
import Checkbox from "@mui/material/Checkbox";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import IconButton from "@mui/material/IconButton";
import ListItemButton from "@mui/material/ListItemButton";

import { boxStylehipContainer, chipStyleChipContainer } from "../../constants/chipContainerStyle";

export default function ChipContainer({ color, labelId }) {
  const [value, setValue] = useState(false);
  return (
    <Box sx={boxStylehipContainer}>
      <ListItemButton
        onClick={() => setValue(!value)}
        sx={{
          padding: "0px",
          height: "32px",
          "&:hover": {
            backgroundColor: "rgba(0, 0, 0, 0)",
          },
        }}
        role={undefined}
        dense
      >
        <Checkbox
          edge="start"
          tabIndex={-1}
          disableRipple
          inputProps={{ "aria-labelledby": labelId }}
          checked={value}
        />
        <CardChip color={color} chipStyle={chipStyleChipContainer} />
      </ListItemButton>
      <IconButton aria-label="settings">
        <EditOutlinedIcon sx={{ fontSize: "18px" }} />
      </IconButton>
    </Box>
  );
}
